.listing {
    margin-bottom: pxToRem(30);

    &--linked-item {
        .listing-item {
            padding: 0;
        }
    }

    &--list-view {
        width: 100%;
    }
}

.listing-item {
    border-radius: 10px;

    &__promoted-tag {
        @include pnp-typeface(text-14-bold, no-margin);
        margin: 0 0.5rem 0 0;
        padding: 0.25rem 0.5rem;
        color: $white;
        background-color: $grey-80;
        border-radius: 4px;
    }

    &--background {
        @include pnp-generate-background-for-colour-map($pnp-primary-colours);
        @include pnp-generate-background-for-colour-map($pnp-neutral-colours);
        &-shadow {
            @include pnp-standard-box-shadow;
        }
    }

    /* 
        Auto generate text colours based on colour maps.
    */
    &--color {
        @include pnp-generate-foreground-for-colour-map($pnp-primary-colours);
        @include pnp-generate-foreground-for-colour-map($pnp-neutral-colours);
    }

    &__content {
        display: flex;
        flex: 1 1 0;
        flex-direction: column;
        width: 100%;
        padding: pxToRem(20);
        word-break: break-word;

        @include pnp-media-breakpoint-up(sm) {
            padding: 1.25rem 1.75rem;
        }
    }

    // Header related elements below
    &__subtitle {
        svg {
            margin-right: pxToRem(5);
        }
    }
    &__subtitle-block {
        display: flex;
        align-items: center;
        color: inherit;

        @include pnp-media-breakpoint-up(sm) {
            margin-right: 1rem;
        }

        svg {
            margin-right: pxToRem(5);
        }
    }

    &__date {
        color: $primary-a;
        @include pnp-media-breakpoint-up(lg) {
            position: absolute;
            bottom: 0.75rem;
            left: 0.75rem;
            padding: 0.75rem 1.25rem;
            text-align: center;
            background-color: rgba($grey-10, 0.7);
            backdrop-filter: blur(10px);

            &--top {
                top: 0.75rem;
                bottom: unset;
            }
        }
    }

    &__tag {
        padding: 0.375rem 0.9375rem;
        color: $primary-a;
        border-radius: 32px;
    }
    &__action {
        margin-top: pxToRem(10);
        color: $primary-a;
        font-weight: 700;
        font-size: 1rem;
        background-color: inherit;
        border: none;
    }

    // Footer related elements below
    &__footer {
        margin-top: auto;
    }

    &__footer-block {
        margin-bottom: pxToRem(10);
        @include pnp-media-breakpoint-up(lg) {
            margin-bottom: 0;
        }
    }
}

/**
    Provide an overlay over video results to mute the img and provide a
    subtle animation to signify that it is a special type of result.
*/
.listing-item--video {
    .listing-item__image-wrapper {
        position: relative;
        overflow: hidden;
        &::after {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 5;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.4);
            content: '';
        }
        &:hover .listing-item__image,
        &:focus .listing-item__image {
            transform: scale(1.2);
        }

        .listing-item__image {
            transition: transform 0.3s ease;
        }

        @media screen and (prefers-reduced-motion: reduce) {
            .listing-item__image {
                transition: none;
            }
        }
    }
}
